import * as React from 'react';
import Typography from '../components/Typography';
import foto from '../assets/profile.jpg';
import Avatar from '@mui/material/Avatar';
import EmailIcon from '@mui/icons-material/Email';
import GitHubIcon from '@mui/icons-material/GitHub';
import LinkedInIcon from '@mui/icons-material/LinkedIn';
import DashboardIcon from '@mui/icons-material/Dashboard';
import Divider from '@mui/material/Divider';
import './product.css'
import Experiences from './Experiences';
import Skills from './Skills';
import Education from './Education';

export default function ProductHero() {
  return (
    <div style={{    width: 860, margin: '0 auto'}}>
      <div className='container'>
                <Avatar
                    alt="Abraham M"
                    src={foto}
                    sx={{ width: 230, height: 230, marginBottom:3 }}
                />
                <Typography variant="h4">
                    Abraham Murillo
                </Typography>
                <div style={{
                    alignItems: "center",
                    display: "flex",
                }}>
                    <EmailIcon style={{color:'gray'}} />
                    <p align="justify" paragraph={true} style={{color:'gray'}}>
                        abraham.murillo04@gmail.com
                    </p>
                </div>
                <Typography variant="h6" style={{marginTop:15,marginBottom:14}}>
                    Full Stack Developer
                </Typography>
                <div style={{
                    alignItems: "center",
                    display: "flex",
                }}>
                    <EmailIcon style={{color:'gray', fontSize:'18px'}} />
                    <p align="justify" className="item-left">
                        Curriculum
                    </p>
                </div>
                <div style={{
                    alignItems: "center",
                    display: "flex",
                }}>
                    <GitHubIcon style={{color:'gray', fontSize:'18px'}} />
                    <p align="justify" className="item-left" >
                        GitHub
                    </p>
                </div>
                <div style={{
                    alignItems: "center",
                    display: "flex",
                }}>
                    <LinkedInIcon style={{color:'gray', fontSize:'18px'}} />
                    <p align="justify" className="item-left">
                        LinkedIn
                    </p>
                </div>
      </div>
      <section style={{width: 550,float: 'right',paddingBottom: 30}}>
        <div style={{marginTop:50,}}>
            <Typography align="left" variant="h5" style={{marginBottom:20}}>
                About me
            </Typography>
        </div>
            <Typography align="justify" paragraph={true}>
                Mechatronics Engineer, with knowledge in programming for the development of web 
                applications, Mobile developer and knowledge in DevOps. Applying the best development 
                practices for its structuring, analysis and consolidation. With experience in various 
                programming languages in web and mobile technologies.
            </Typography>
            <Typography align="justify" paragraph={true}>
                I am currently a Full Stack developer at the National Institute of Statistics and Geography 
                (INEGI), in the city of Aguascalientes, Aguascalientes, Mexico.
            </Typography>
            <Typography align="justify" paragraph={true}>
                With a great interest in web development, so I started to study and implement code in different 
                languages and platforms.
            </Typography>
            <Divider />
        <div style={{marginTop:15,}}>
            <Education/>
            <Divider />
            <Skills/>
            <Divider />
            <Experiences />
        </div>
      </section>
    </div>
  );
}

